interface Step {
  step: number;
  page: string;
}

const state: Step = {
  step: 1,
  page: 'Description',
};
const mutations = {
  SET_STEP: (state: Step, step: { step: number, page: string }) => {
    state.step = step.step;
    state.page = step.page;
  },
};
const actions = {
  nextStep: ({ state, commit }: { state: Step, commit: Function }) => {
    switch (state.page) {
      case 'Description': {
        commit('SET_STEP', {
          step: state.step,
          page: 'Photo',
        });
        break;
      }
      case 'Photo': {
        commit('SET_STEP', {
          step: state.step,
          page: 'Accept',
        });
        break;
      }
      case 'Accept': {
        if (state.step < 3) {
          commit('SET_STEP', {
            step: state.step + 1,
            page: 'Description',
          });
          break;
        } else {
          commit('SET_STEP', {
            step: state.step + 1,
            page: 'Success',
          });
          break;
        }
      }
      default: break;
    }
  },
  prevStep: ({ state, commit }: { state: Step, commit: Function }) => {
    commit('SET_STEP', {
      step: state.step,
      page: 'Photo',
    });
  },
};
const getters = {
  step: (state: Step) => state.step,
  page: (state: Step) => state.page,
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
