

















import Vue from 'vue';
import { mapGetters } from 'vuex';

export default Vue.extend({
  name: 'Photo',
  computed: {
    ...mapGetters('stream', [
      'mediaStream',
    ]),
    ...mapGetters('step', [
      'step',
    ]),
  },
  created(): void {
  },
  async mounted(): Promise<void> {
    const w = window.screen.width;
    (this.$refs.video as HTMLVideoElement).style.clipPath = `ellipse(calc(${w}px / 2.5) calc(${w}px / 2) at center calc(100vh / 2 - 50px))`;
    (this.$refs.video as HTMLVideoElement).srcObject = this.mediaStream;
    await (this.$refs.video as HTMLVideoElement).play();
  },
});
